.About img {
  max-width: 25vw;
  filter: drop-shadow(1rem 1rem 1rem rgba(0, 0, 0, 0.5));
  -webkit-filter: drop-shadow(1rem 1rem 1rem rgba(0, 0, 0, 0.5));
}
.header_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 800px) {
  .About img {
    max-width: 70vw;
  }
  .About {
    margin-top: 5rem;
  }
}
