@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400&display=swap);
.Socials {
  display: flex;
  font-size: 2.5rem;
}

.Socials a {
  height: 30%;
  color: #fff;
  margin: 0 0.5rem;
  text-shadow: 0 0 1rem #000;
  display: block;
  transition: 0.3s ease-in-out;
  -webkit-transition: 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out;
  -ms-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
}

.Socials a:hover {
  text-shadow: 0 0.7rem 0.5rem #000;
  transform: translateY(-5px);
  -webkit-transform: translateY(-5px);
  -moz-transform: translateY(-5px);
  -ms-transform: translateY(-5px);
  -o-transform: translateY(-5px);
}

@font-face {
  font-family: "aristotelica";
  src: url(/static/media/aristotelica.display-trial-regular.42c60a5f.ttf);
}

.saad {
  font-family: aristotelica;
  font-size: 4rem;
  letter-spacing: 0.5rem;
}

.services {
  padding-left: 0;
  list-style: none;
}

.services a {
  text-decoration: none;
  color: #fff;
  line-height: 2rem;
  position: relative;
  padding-bottom: 0.3rem;
}

.services a::after {
  position: absolute;
  content: "";
  background: white;
  width: 0;
  height: 1px;
  bottom: 0;
  left: 0;
  transition: 0.3s ease-in-out;
  -webkit-transition: 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out;
  -ms-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
}

.services a:hover::after {
  width: 100%;
}

.end_text {
  background: #111;
}
.end_text p {
  margin: 0;
  padding: 0;
  line-height: 8vh;
}

@media (max-width: 768px) {
  .Socials {
    font-size: 1.7rem;
  }
  .saad {
    font-size: 2.5rem;
  }
}

.header {
  opacity: 0;
  text-align: center;
  animation: animate 1s ease-in-out 1.5s forwards;
  -webkit-animation: animate 1s ease-in-out 1.5s forwards;
}

.header h2,
.header h1 {
  text-shadow: 0 0 0.2rem red, 0 0 0.4rem red, 0 0 0.6rem red, 0 0 0.8rem red,
    0 0 1rem red, 0 0 1.2rem red;
}

.header h2 {
  font-size: 2.5rem;
}

.header h1 {
  font-size: 4rem;
}

@-webkit-keyframes animate {
  0% {
    transform: translateY(-50%);
    opacity: 0;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
  }
}

@keyframes animate {
  0% {
    transform: translateY(-50%);
    opacity: 0;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .header h2 {
    font-size: 2rem;
  }
  .header h1 {
    font-size: 1.7rem;
  }
}

.About img {
  max-width: 25vw;
  filter: drop-shadow(1rem 1rem 1rem rgba(0, 0, 0, 0.5));
  -webkit-filter: drop-shadow(1rem 1rem 1rem rgba(0, 0, 0, 0.5));
}
.header_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 800px) {
  .About img {
    max-width: 70vw;
  }
  .About {
    margin-top: 5rem;
  }
}

.Contact {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 65vh;
  padding: 1rem 2rem 1rem 2rem;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
  background: rgba(255, 255, 255, 0.3);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.Contact form {
  width: 70%;
}

textarea {
  resize: none;
}

@media (max-width: 786px) {
  .Contact form {
    width: 100%;
  }
}

.Register {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 65vh;
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
  background: rgba(255, 255, 255, 0.3);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.parent {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 768px) {
  .register_area {
    padding-left: 1rem;
    padding-right: 1rem;
  }

  .Register {
    margin-top: 3rem;
  }
}

.Login {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 65vh;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 1rem;
  background: rgba(255, 255, 255, 0.3);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
}

.Login form {
  width: 50%;
}

@media (max-width: 768px) {
  .Login form {
    width: auto;
    width: initial;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}
body {
  background: linear-gradient(90deg, #cc2b5e, #753a88);
}
#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #fff;
}
.nav_bg {
  background: rgba(255, 255, 255, 0.4);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}
.border-b {
  border-bottom: 2px solid #fff;
}
.main_div {
  min-height: 100vh; /* 82vh */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logoutBtn {
  margin-left: 0.5rem;
}

@media (max-width: 800px) {
  .logoutBtn {
    margin-top: 0.5rem;
    margin-left: 0;
    width: 4rem;
    text-align: center;
  }
}

