.header {
  opacity: 0;
  text-align: center;
  animation: animate 1s ease-in-out 1.5s forwards;
  -webkit-animation: animate 1s ease-in-out 1.5s forwards;
}

.header h2,
.header h1 {
  text-shadow: 0 0 0.2rem red, 0 0 0.4rem red, 0 0 0.6rem red, 0 0 0.8rem red,
    0 0 1rem red, 0 0 1.2rem red;
}

.header h2 {
  font-size: 2.5rem;
}

.header h1 {
  font-size: 4rem;
}

@keyframes animate {
  0% {
    transform: translateY(-50%);
    opacity: 0;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .header h2 {
    font-size: 2rem;
  }
  .header h1 {
    font-size: 1.7rem;
  }
}
