.Socials {
  display: flex;
  font-size: 2.5rem;
}

.Socials a {
  height: 30%;
  color: #fff;
  margin: 0 0.5rem;
  text-shadow: 0 0 1rem #000;
  display: block;
  transition: 0.3s ease-in-out;
  -webkit-transition: 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out;
  -ms-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
}

.Socials a:hover {
  text-shadow: 0 0.7rem 0.5rem #000;
  transform: translateY(-5px);
  -webkit-transform: translateY(-5px);
  -moz-transform: translateY(-5px);
  -ms-transform: translateY(-5px);
  -o-transform: translateY(-5px);
}

@font-face {
  font-family: "aristotelica";
  src: url("../fonts/aristotelica.display-trial-regular.ttf");
}

.saad {
  font-family: aristotelica;
  font-size: 4rem;
  letter-spacing: 0.5rem;
}

.services {
  padding-left: 0;
  list-style: none;
}

.services a {
  text-decoration: none;
  color: #fff;
  line-height: 2rem;
  position: relative;
  padding-bottom: 0.3rem;
}

.services a::after {
  position: absolute;
  content: "";
  background: white;
  width: 0;
  height: 1px;
  bottom: 0;
  left: 0;
  transition: 0.3s ease-in-out;
  -webkit-transition: 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out;
  -ms-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
}

.services a:hover::after {
  width: 100%;
}

.end_text {
  background: #111;
}
.end_text p {
  margin: 0;
  padding: 0;
  line-height: 8vh;
}

@media (max-width: 768px) {
  .Socials {
    font-size: 1.7rem;
  }
  .saad {
    font-size: 2.5rem;
  }
}
